.presentation {
    /*border: 1px solid white;*/
    width: 80%;
    display: flex;
}

.imageField {
    position: relative;
    height: 300px;
}

.info {
    /*display: flex;*/
    margin-left: 5%;
}

.avatar {
    /*box-shadow: black 4px 4px;*/
    /*border: 1px solid white;*/
    border-radius: 8px;
    width: 200px;
    height: 300px;
    object-fit: cover;
}

.input {
    opacity: 0;
    /*border: 1px solid white;*/
    position: absolute;
    top: 2%;
    border-radius: 8px;
    width: 200px;
    height: 300px;
}

.inputHover {
    pointer-events: none;
    /*display: none;*/
    width: 120px;
    opacity: 0;
    z-index: 20;
    box-sizing: border-box;
    /*border: 10px solid white;*/
    position: absolute;
    top: 18%;
    left: 20%;
    transition: 1s;
    filter: invert(80%);
}

.imageField:hover .inputHover{
    opacity: 0.8;
}

